import {mapState} from "vuex";

export default {
    name: "bd-service",
    props: {
        name: String,
        url: String
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall
        }),
    },
    methods: {
        router(url) {
            uni.navigateTo({
                url: url
            })
        },
        makePhoneCall() {
            if (this.mall.setting.contact_tel) {
                uni.makePhoneCall({
                    phoneNumber: this.mall.setting.contact_tel
                })
            }
        }
    }
}
